import request from '../../util/request';

export default {
    name: 'My',
    data () {
        return {
            displayInfo: [
                {
                    icon: 'comment',
                    title: '意见反馈'
                },
                {
                    icon: 'settings',
                    title: '设置'
                }
            ]
        };
    },
    onShareAppMessage: function (res) {
        return {
            title: '畅游看吧',
            path: '/pages/navigation/HomePage',
            imageUrl: '/static/image/share.png'
        };
    },
    computed: {
        UserInfo () {
            return this.$store.state.userInfo;
        },
        sexClazz () {
            let clazz = '';
            if (this.$store.state.userInfo) {
                if (this.$store.state.userInfo.gender === 1) {
                    clazz = 'cuIcon-male bg-blue';
                } else if (this.$store.state.userInfo.gender === 2) {
                    clazz = 'cuIcon-female bg-pink';
                }
            }
            return clazz;
        },
        avatarClazz () {
            let clazz = '';
            if (this.$store.state.userInfo && this.$store.state.userInfo.avatar) {
                clazz = `background-image:url(${this.$store.state.userInfo.avatar})`;
            }
            return clazz;
        }
    },
    methods: {
        tapBtn (type) {
            switch (type) {
                case 'comment':
                    uni.navigateTo({
                        url: '/pages/comment/Comment'
                    });
                    break;
                case 'settings':
                    uni.navigateTo({
                        url: '/pages/settings/Settings'
                    });
                    break;
            }
        },
        wxBtn () {
            uni.login({
                success: response2 => {
                    // 获取用户信息
                    uni.getUserInfo({
                        provider: 'weixin',
                        success: response3 => {
                            uni.showLoading({
                                title: '登陆中',
                                mask: true
                            });
                            let params = {
                                condition: {
                                    code: response2.code,
                                    avatar: response3.userInfo.avatarUrl,
                                    nickName: response3.userInfo.nickName,
                                    gender: response3.userInfo.gender
                                }
                            };
                            request.post('/users/weixin/getUsersInfo', params).then(data => {
                                uni.hideLoading();
                                if (data.status === 200) {
                                    let result = JSON.stringify(data.data[0]);
                                    uni.setStorage({
                                        key: 'userInfo',
                                        data: result
                                    });
                                    this.$store.commit('SET_USERINFO', data.data[0]);
                                    this.$emit('userInfo', data.data[0]);
                                } else {
                                    uni.showToast({
                                        title: '获取用户信息失败',
                                        duration: 1000,
                                        icon: 'none'
                                    });
                                }
                            }).catch(e => {
                                uni.hideLoading();
                                uni.showToast({
                                    title: '获取用户信息失败',
                                    duration: 1000,
                                    icon: 'none'
                                });
                            });
                        },
                        fail: reject3 => {
                            // doNothing...
                        }
                    });
                },
                fail: response2 => {
                    debugger;
                    uni.showToast({
                        title: '登录失败',
                        duration: 1000,
                        icon: 'none'
                    });
                }
            });
        }
    }
};
